const Button = videojs.getComponent('Button')
let ptzFn = null
class PtzButton extends Button {
  constructor (player, options = {}) {
    super(player, options)
    // console.log(player, options)
    this.controlText('云台控制')
    this.show = false
    // this.on(player, "play", this.handlePlay);
    // this.on(player, "pause", this.handlePaused);
    // this.on(this.el(), ["click", "tap"], this.handleControlClick);
  }

  buildCSSClass () {
    return 'ali-icon-font  ali-icon-PTZ ptz-btn'//icon aufontAll fast-forward h-icon-all-fast-backward
    // return `vjs-play-control ${super.buildCSSClass()}`
  }

  static setFn (fn) {
    ptzFn = fn
  }

  handleClick () {
    this.show = !this.show
    if (typeof ptzFn === 'function') {
      ptzFn(this.show)
    }
    // player.playlist.next();
  }
}

// Component.registerComponent('PtzButton', PtzButton)

export {
  PtzButton
}
